
.toast {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.toast-message {
  margin-right: 10px;
}

.toast-close {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  width: 15px;
  height: 15px;
  position: relative;
}

.toast-close::before,
.toast-close::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 2px;
  background-color: white;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.toast-close::before {
  transform: translateY(-50%) rotate(45deg);
}

.toast-close::after {
  transform: translateY(-50%) rotate(-45deg);
}

.toast-info {
  background-color: rgba(76, 175, 80, 0.7);
}

.toast-success {
  background-color: rgba(38, 166, 91, 0.7);
}

.toast-warning {
  background-color: rgba(255, 193, 7, 0.7);
}

.toast-error {
  background-color: rgba(244, 67, 54, 0.7);
}
